body {
  margin: 0;
  padding: 0;
  width: 100%;
}
a {
  text-decoration: none;
  color: black;
}

img {
  width: 40%;
  height: 17em;
}

.navigation {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  margin: 1em;
}

.list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-rows: auto;
  margin-top: 1em;
}

